<template>
  <div class="home-page">
    <div class="layout">
      <Layout>
        <Sider ref="sider" hide-trigger collapsible :collapsed-width="0" v-model="isCollapsed">
        <Menu  accordion theme="dark" active-name="1" width="auto" @on-select="handleSelected">
          <Submenu name="1">
            <template slot="title"><Icon type="ios-navigate" />随時処理</template>
          </Submenu>

          <Submenu name="2">
            <template slot="title"><Icon type="ios-paper" />申請承認</template>
          </Submenu>

          <Submenu name="3">
            <template slot="title"><Icon type="ios-calendar" />月次処理</template>
          </Submenu>

          <Submenu name="4">
            <template slot="title"><Icon type="ios-cloudy-night" />有休管理</template>
          </Submenu>

          <Submenu name="5">
            <template slot="title">
              <Icon type="ios-albums" />登録管理</template>
            <MenuItem name="5-1">社員区分登録</MenuItem>
            <MenuItem name="5-2">勤務体系登録</MenuItem>
            <MenuItem name="5-3">所属登録</MenuItem>
            <MenuItem name="5-4">社員登録・ICカード割当</MenuItem>
            <MenuItem name="5-5">勤怠管理対象外社員登録</MenuItem>
            <MenuItem name="5-6">所属長兼任設定</MenuItem>
            <MenuItem name="5-7">祝日登録</MenuItem>
            <MenuItem name="5-8">時間休設定</MenuItem>
            <MenuItem name="5-9">ICカード登録要求一覧</MenuItem>
            <MenuItem name="5-10">端末登録</MenuItem>
            <MenuItem name="5-11">環境設定</MenuItem>
          </Submenu>
        </Menu>
        </Sider>
          <div class="collapse-icon">
          <Icon @click.native="collapsedSider" :class="rotateIcon"  type="md-arrow-back" size="24"></Icon>
          </div>
        <Layout :style="warp">
          <Content :style="content">
            <div v-if="show">
              <Divider dashed orientation="left">
                <h2>{{ choose }}</h2>
              </Divider>
              <TableZ @is-check="onIsCheck" ref="tableData"></TableZ>
              <!-- 三个按钮 -->
              <div class="button-group">
                <!-- 追加按钮 -->
                <Button type="success" @click="addModal = true" icon="md-add">追加</Button>
                <Modal v-model="addModal">
                  <Divider dashed orientation="left" style="margin-bottom: 30px">所属登録</Divider>
                  <Input v-model=value.code size="large" placeholder="コード" style="margin-bottom: 10px; width:95%" />
                  <Input v-model=value.name size="large" placeholder="表示名称" style="margin-bottom: 10px; width:95%" />
                  <div slot="footer">
                    <Button type="text" @click="cancel">キャンセル</Button>
                    <Button type="success" size="large" @click="add">追加</Button>
                  </div>
                </Modal>

                <Button type="primary" icon="ios-cloud">更新</Button>

                <!-- 削除按钮 -->
                <Button type="error" @click="deleteModal = true" icon="md-trash">削除</Button>
                <Modal v-model="deleteModal">
                  <Divider dashed orientation="left" style="margin-bottom: 30px">注意</Divider>
                  <h2 style="color: rgb(252,13,27)">削除してよろしいでしょうか</h2>
                  <div slot="footer">
                    <Button type="text" @click="cancel">キャンセル</Button>
                    <Button type="error" size="large" @click="remove">削除</Button>
                  </div>
                </Modal>

              </div>

            </div>
            <Divider dashed orientation="left" v-else>
              <h2>{{ choose }}</h2>
            </Divider>
          </Content>
        </Layout>
      </Layout>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.button-group {
  margin-top: 10px;
  button {
    margin-right: 10px;
  }
}
.collapse-icon {
  position: fixed;
  left: 6.5rem;
  bottom: 5rem;
  @include size(2rem, 2rem);
  padding: 0;
  background: transparent;
  color: $box-title;
}
.rotate-icon {
  transform: rotate(-180deg);
  color: $box-title-small;
}
</style>
<script>
import TableZ from './TableZ.vue'
import { mapState } from 'vuex'
import { mapActions } from 'vuex'
export default {
  name: 'HomePage',
  components: {
    TableZ
  },
  data: function() {
    return {
      warp: {
        padding: '24px'
      },
      content: {
        padding: '24px',
        minHeight: '100vh',
        background: '#fff',
        border: '1px solid transparent',
        'border-radius': '6px',
        'box-shadow': '0 2px 7px rgba(0,0,0,.15)'
      },
      show: false,
      choose: '登録管理の所属登録を選択してください',
      deleteModal: false,
      addModal: false,
      isCollapsed: false,
      localPassWord: 'iii',
      index: [],
      value: {
        code: '',
        name: ''
      }
    }
  },
  computed: {
    rotateIcon() {
      return ['menu-icon', this.isCollapsed ? 'rotate-icon' : '']
    },
    ...mapState({
      account: state => state.account,
      accountAlias: 'account',

      passWord: state => state.passWord,
      passWordAlias: 'passWord',

      // 可以像这样操作本地与state的数据
      updateID(state) {
        return state.id + this.localPassWord
      }
    })
  },
  methods: {
    ...mapActions(['actionUpdatePassWord']),
    handleSelected(name) {
      if (name) {
        if (name === '5-3') {
          this.choose = '所属登録'
          this.collapsedSider()
          return (this.show = true)
        }
      }
      this.show = false
      return (this.choose = 'この機能がまだ開発中ので、再選択してください。')
    },
    collapsedSider() {
      this.$refs.sider.toggleCollapse()
    },
    add() {
      let code = this.value.code
      let name = this.value.name
      this.$refs.tableData.data.push({ code, name })
      this.addModal = false
      this.$Message.success('追加完了')
    },
    onIsCheck: function(data, isOk) {
      if (isOk) {
        let index = [...this.index, data]
        this.index = [...new Set(index)]

        // 点一下checkbox就改了vuex里的password
        // const newPassWord = { password: '9527' }
        // this.actionUpdatePassWord(newPassWord).then(() => {
        //   this.$Message.success('パスワード修正完了')
        //   console.log(this.passWord)
        // })
      }
      if (isOk === false) {
        this.index = this.index.filter(e => e !== data)
      }
    },
    remove() {
      let data = this.index
        .concat(this.$refs.tableData.data)
        .filter(function(v, i, arr) {
          return arr.indexOf(v) === arr.lastIndexOf(v)
        })
      if (data) {
        this.$refs.tableData.data = data
      }
      this.deleteModal = false
      this.$Message.success('削除完了')
    },
    cancel() {
      if (this.addModal) {
        this.addModal = false
      }
      if (this.deleteModal) {
        this.deleteModal = false
      }
    }
  }
}
</script>
